	.sidebar-content {
	    .inner-sidebar.affix {
	        position: fixed !important;
	    }
	}

	.widget-quotes {
	    .media {
	        .media-left {
	            padding-right: $padding-md;
	        }
	        .media-body {
	            color: $color-dark-grey;
	            font-size: 15px;
	            margin-left: $margin-sm;
	            h4 {
	                color: black;
	                margin: $margin-sm 0;
	            }
	        }
	    }
	}

	.widget-most-popular,
	.widget-news-topic {
	    .media {
	        margin-top: 0px;
	        .media-left {
	            padding-right: 20px;
	            .most-popular-rank {
	                height: $most-popular-rank-height;
	                font-weight: bold;
	                font-size: 24px;
	                text-align: center;
	                vertical-align: middle;
	                display: table-cell;
	                color: $color-info;
	                .rank {
	                    width: $most-popular-rank-width;
	                }
	            }
	        }
	        .media-body {
	            .media-heading {
	                font-weight: bold;
	                font-size: 18px;
	            }
	            height: $most-popular-rank-height;
	            vertical-align: middle;
	        }
	    }
	}

	.widget-most-popular {
	    .media {
	        margin-bottom: 10px;
	    }
	}

	.widget-news-topic {
	    .media {
	        .media-left {
	            .most-popular-rank {
	                height: $news-topic-height;
	            }
	        }
	        .media-body {
	            height: $news-topic-height;
	        }
	    }
	}

	.widget-news-aggregator {
	    .media {
	        .media-left {
	            .media-object {
	                height: 60px;
	                width: 60px;
	                background-position: center middle;
	                background-attachment: clip;
	                background-size: auto 60px;
	                background-repeat: none;
	            }
	        }
	        .media-body {
	            h5.media-category-name {
	                margin-top: 5px;
	            }
	            p.media-heading {
	                padding: 5px 0;
	                a {
	                    font-size: 16px;
	                }
	            }
	        }
	    }
	}

	@media only screen and (min-width: 1200px) {
	    .inner-sidebar.affix {
			width : 350px;
		}
	}